body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-grid-container {
  display: grid;
  grid-template-columns: 1fr;
}
.overlay {
  position: absolute;
  width: 100vw;
  height: calc(100vw * 9 / 16);
  top: 0;
}

.player-wrapper {
  /* background-color: #111; */
  /* filter: opacity(.5); */
  /* filter: invert(.5); */
  position: relative;
  padding-top: calc(100%/(568/320)) /* Player ratio: 100 / (1280 / 720) */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}
.logo {
  height: 80px;
  z-index: 2;
}

.logo:hover {
  cursor: pointer;
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
}

.nav-wrapper {
  position: absolute;
  bottom: 10vh;
  right: 10vh;
}
.header-wrapper {
  position: relative;
}
.Us {
  margin-bottom: 10vw;
}

h2 {
  font-size: 44px;
}

p {
  font-size: 32px;
}

.flex-center {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.us-wrapper {
  color: #fff;
  background-image: url(/elevate-innovate-dev/static/media/stock-teamwork.bcf283ed.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Montserrat';
  height: calc( 100vw * 2 / 3);
  padding: 300px;
  position: relative;
}

.us-wrapper:after {
  position: absolute;
  content:"";
  height:100%;
  width:100%;
  top:0;
  left:0;
  background: 
    -webkit-linear-gradient(left, rgba(0, 0, 0, 0) 0%,rgb(0, 0, 0) 80%),
    -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0) 0%,rgba(0, 0, 0, 1) 100%);
  background: 
    linear-gradient(to right, rgba(0, 0, 0, 0) 0%,rgb(0, 0, 0) 80%),
    linear-gradient(to top, rgba(0, 0, 0, 0) 0%,rgba(0, 0, 0, 1) 100%);
  z-index: 2;
}

.us-wrapper > * {
  text-align: center;
  z-index: 99;
}
.col {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px 10px;;
}

.image {
  width: 100%;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.clients-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 60px;
  margin: 10vw 20vw;
}

.clients-grid > * {
  width: 100%;
}

.clients-title {
  display: flex;
  justify-content: center;
}

.logo {
  max-height: 20vw;
  max-width: 20vw;
  height: auto;
  width: auto;
}

.logo-container {
  align-items: center;
  display: flex;
  justify-content: center;
}
.Contact {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10vw;
}

p {
  margin: 10px;
}

.links > a {
  margin: 10px;
}
